
import { defineComponent } from 'vue'
import MainPage from './pages/MainPage.vue';
import Header from "./components/Header.vue"

export default defineComponent({
  components: {
    Header,
    MainPage,
  },
  data () {
    return {
      mode: 'dark'
    }
  },
  methods: {
    toggle() {
      if (this.mode === 'dark') {
        this.mode = 'light'
      } else {
        this.mode = 'dark'
      }
    }
  }
})
